import React from 'react';
import {
  Drawer,
  Box,
  Typography,
  List,
  ListItem,
  IconButton,
  Button,
  Stack,
  Divider
} from '@mui/material';
import { Close as CloseIcon } from '@mui/icons-material';

interface CartDrawerProps {
  open: boolean;
  onClose: () => void;
  items: any[]; // 后续可以定义具体的类型
}

export default function CartDrawer({ open, onClose }: CartDrawerProps) {
  const { items, removeItem, updateQuantity, total, clearCart } = useCart();
  const [isCheckingOut, setIsCheckingOut] = useState(false);
  const [paymentStatus, setPaymentStatus] = useState<'idle' | 'processing' | 'success' | 'error'>('idle');

  const handleCheckout = async () => {
    setIsCheckingOut(true);
    setPaymentStatus('processing');

    // 模拟支付过程
    try {
      await new Promise(resolve => setTimeout(resolve, 2000)); // 模拟网络请求
      setPaymentStatus('success');
      clearCart();
      setTimeout(() => {
        setIsCheckingOut(false);
        setPaymentStatus('idle');
        onClose();
      }, 1500);
    } catch (error) {
      setPaymentStatus('error');
      setTimeout(() => {
        setPaymentStatus('idle');
      }, 1500);
    }
  };

  return (
    <>
      <Drawer anchor="right" open={open} onClose={onClose}>
        <Box sx={{ width: 400, p: 2 }}>
          <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ mb: 2 }}>
            <Typography variant="h6">Shopping Cart ({items.length})</Typography>
            <IconButton onClick={onClose}>
              <CloseIcon />
            </IconButton>
          </Stack>

          <List>
            {items.map((item) => (
              <ListItem
                key={item.id}
                secondaryAction={
                  <IconButton edge="end" onClick={() => removeItem(item.id)}>
                    <DeleteIcon />
                  </IconButton>
                }
              >
                <ListItemAvatar>
                  <Avatar src={item.imageUrl} variant="square" />
                </ListItemAvatar>
                <ListItemText
                  primary={item.name}
                  secondary={
                    <Stack direction="row" alignItems="center" spacing={1}>
                      <Typography variant="body2">${item.price}</Typography>
                      <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <IconButton
                          size="small"
                          onClick={() => updateQuantity(item.id, Math.max(0, item.quantity - 1))}
                        >
                          <RemoveIcon fontSize="small" />
                        </IconButton>
                        <Typography sx={{ mx: 1 }}>{item.quantity}</Typography>
                        <IconButton
                          size="small"
                          onClick={() => updateQuantity(item.id, item.quantity + 1)}
                        >
                          <AddIcon fontSize="small" />
                        </IconButton>
                      </Box>
                    </Stack>
                  }
                />
              </ListItem>
            ))}
          </List>

          <Divider sx={{ my: 2 }} />

          <Typography variant="h6" sx={{ mb: 2 }}>
            Total: ${total.toFixed(2)}
          </Typography>

          <Button
            variant="contained"
            fullWidth
            disabled={items.length === 0 || paymentStatus === 'processing'}
            onClick={handleCheckout}
          >
            {paymentStatus === 'processing' ? (
              <CircularProgress size={24} color="inherit" />
            ) : (
              'Checkout'
            )}
          </Button>
        </Box>
      </Drawer>

      <Dialog open={paymentStatus === 'success'}>
        <DialogTitle>Payment Successful!</DialogTitle>
        <DialogContent>
          <Typography>
            Thank you for your purchase. Your order has been confirmed.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={onClose}>Close</Button>
        </DialogActions>
      </Dialog>
    </>
  );
} 